.Joke {
  display: flex;
  border-bottom: 2px solid #eeeeee;
  text-align: left;
  align-items: center;
  justify-content: center;
  font-weight: 400;
  padding: 1rem;
}
.face img {
  /* opacity: 0.6; */
  width: 50px;
  border-radius: 50%;
  padding: 5px;
  margin-right: 15px;
  box-shadow: inset 0px -1px 15px -2px #c7c7c7;

}

.Joke-buttons {
  display: flex;
  margin-right: 1rem;
  align-items: center;
  justify-content: center;
  width: 15%;
}

.Joke-text {
  font-size: 1rem;
  /* font-weight: 500; */
  font-family: 'Cinzel', serif;}

.Joke-text span {
  color: rgb(141, 141, 141);
  font-size: 0.6rem;
  font-style: italic;
  display: inline-block;
  /* width: 90%; */
  text-decoration: none;
  margin-right: 10%;
}

.Joke-text :last-child {
  text-align: right;
  float: right;
}
.Joke-addButton {
  border-radius: 50%;
  border: solid;
  background-color: #fbfdf9;
  line-height: 20px;
  border-color:whitesmoke;
  font-style: oblique;
  box-shadow: inset 1px 6px 12px rgb(246, 239, 239), inset -1px -10px 5px rgb(235, 238, 235), 1px 2px 1px rgb(235, 235, 235);
  color: rgb(158, 150, 150);
  text-shadow: 1px 1px #d1cece;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.Joke-votes {
  width: 30px;
  height: 30px;
  line-height: 30px;
  border-radius: 12%;
  text-align: center;
  font-size: 20px;
  font-weight: 300;
  border: 3px solid rgb(184, 181, 181);
  box-shadow: 0 10px 38px rgba(0, 0, 0, 0.2), 0 10px 12px rgba(0, 0, 0, 0.1);
}

.fa-arrow-up,
.fa-arrow-down {
  font-size: 1.5em;
  margin: 10px;
  cursor: pointer;
  transition: all 0.3s;
}

.fa-arrow-up:hover {
  animation-name: move-up;
  animation-duration: 0.7s;
  animation-timing-function: ease-out;
  color: #8bc34a;
}

@keyframes move-up {
  0%,
  50%,
  100% {
    transform: translateY(0);
  }
  25%,
  75% {
    transform: translateY(-3px);
  }
}
.fa-arrow-down:hover {
  animation-name: move-down;
  animation-duration: 0.7s;
  animation-timing-function: ease-out;
  color: #f44336;
}
@keyframes move-down {
  0%,
  50%,
  100% {
    transform: translateY(0);
  }
  25%,
  75% {
    transform: translateY(3px);
  }
}

.Joke-smiley {
  font-size: 3rem;
  margin-left: auto;
  border-radius: 50%;
  box-shadow: 0 10px 38px rgba(0, 0, 0, 0.2), 0 10px 12px rgba(0, 0, 0, 0.1);
}

.Joke-smiley:hover {
  animation-name: spin;
  animation-duration: 0.7s;
  animation-timing-function: ease-in-out;
  animation-delay: 0s;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  30% {
    transform: rotate(20deg);
  }
  60% {
    transform: rotate(0deg);
  }
  80% {
    transform: rotate(10deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
.close{
  color:rgb(93, 92, 92);
  margin-bottom: 60px;
  margin-right: 5px;;
  opacity: .3;
  font-size: 1.2rem;
}
@media screen and (max-width: 480px) {
  .Joke-text {
    font-size: 1rem;
    font-weight: 500;
    font-family: 'Cinzel', serif;}
}